.form {
  background-color: #011c40;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);
  padding: 43px 50px 50px 50px;
  min-height: 455px;
}
@media (max-width: 1023px) {
  .form {
    padding-top: 45px;
    width: 59.5%;
  }
}
@media (max-width: 767px) {
  .form {
    padding: 32px 30px 41px 30px;
    width: 100%;
  }
}

.form__field {
  display: flex;
  position: relative;
  flex-direction: column;
}

.form__description {
  font-weight: 500;
  line-height: 27px;
  font-size: 18px;
  margin-top: 0;
  margin-bottom: 27px;
}
@media (max-width: 767px) {
  .form__description {
    text-align: center;
    margin-bottom: 26px;
  }
}

.form__field input {
  border: none;
  background-color: #f9fbfd;
  padding: 13px 30px 13px 16px;
  margin-bottom: 18px;
}
.form__field input:hover {
  background-color: #e8e8dc;
}
@media (max-width: 767px) {
  .form__field input {
    margin-bottom: 19px;
    padding-bottom: 12px;
  }
}

.form__field input::placeholder {
  color: #011c40;
  opacity: 0.7;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
}

.form__field label {
  position: relative;
  color: #f9fbfd;
  font-size: 10px;
  line-height: 13px;
  padding-left: 24px;
  margin-bottom: 31px;
  cursor: pointer;
  margin-top: 5px;
}
@media (max-width: 767px) {
  .form__field label {
    line-height: 12px;
    margin-bottom: 26px;
    margin-top: 0;
    padding-left: 28px;
  }
}

.form__field input[type=checkbox] {
  top: 68%;
  left: 3%;
}

.form__field label::before {
  content: "";
  position: absolute;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
@media (max-width: 767px) {
  .form__field label::before {
    width: 20px;
    height: 20px;
  }
}

.form__field input:checked + label::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(40%, -60%);
  width: 10px;
  height: 10px;
  background-image: url("../img/svg/checkmark.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 767px) {
  .form__field input:checked + label::after {
    top: 55%;
  }
}

.form__field input:focus-visible + label::before {
  background-color: #e8e8dc;
}

.form__field button {
  border: none;
  background-color: #f9fbfd;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  color: #011c40;
  line-height: 21px;
  font-size: 14px;
  padding: 13px 0 9px 0;
  user-select: none;
  cursor: pointer;
}
.form__field button:hover {
  background-color: #e8e8dc;
}
.form__field button:active {
  opacity: 0.8;
}

